import styled from 'styled-components';

const StyledButtonTooltip = styled.button.attrs(() => ({
  type: 'button',
}))`
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  // ToDo: consider using a more backwards compatible method as focus-visible is recent
  &:focus-visible {
    .tooltiptext {
      display: ${(props) => (!props.disabled || props.showTooltip) && 'initial'};
    }
  }
  @media (hover: hover) {
    &:hover {
      .tooltiptext {
        display: ${(props) => (!props.disabled || props.showTooltip) && 'initial'};
      }
    }
  }
  &:active {
    .tooltiptext {
      display: ${(props) => (!props.disabled || props.showTooltip) && 'initial'};
    }
  }
  position: relative;
  .tooltiptext {
    display: none;
    white-space: nowrap;
    bottom: 130%;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 7px 10px;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    border-radius: 10px;
    position: absolute;
    z-index: 1;
    &:after {
      content: ' ';
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -8px;
      border-width: 8px;
      border-style: solid;
      border-color: black transparent transparent transparent;
    }
  }
`;

export default StyledButtonTooltip;
