// @flow

import { validate } from 'uuid';

import { getSessionUuid, setSessionUuid, createSessionUuid, extractUuidFromPathname } from 'utils/uuid';

const setupSession = () => {
  const currentSessionUuid = getSessionUuid();
  const { pathname } = window.location;
  const uuidFromPathname = extractUuidFromPathname(pathname);

  if (validate(uuidFromPathname)) {
    setSessionUuid(uuidFromPathname);
  } else if (pathname === '/') {
    if (validate(currentSessionUuid)) {
      return;
    }
    createSessionUuid();
  }
  // console.error('failed to get a session id', getSessionUuid());
  // else if any error case, navigate to error page
  // this can also have a 'null' string as pathname
};

export default setupSession;
