// @flow

import sessionStore from 'stores/SessionStore';
import trackEvent from 'services/analytics/Ahoy';

import shareAppUrl from './ShareAppUrl';

const setupEventListeners = () => {
  sessionStore.webexApp
    .listen()
    .then(() => {
      sessionStore.webexApp.on('meeting:roleChanged', (roles) => {
        sessionStore.setRoles(roles);
        shareAppUrl(); // fix for share button disappearing
      });
      sessionStore.webexApp.on('application:displayContextChanged', (display) => {
        sessionStore.setDisplayContext(display);
      });
      sessionStore.webexApp.on('application:themeChanged', (theme) => {
        sessionStore.setTheme(theme);
      });

      sessionStore.webexApp.on('application:shareStateChanged', (state) => {
        if (state) {
          trackEvent('open_together');
        } else {
          trackEvent('stop_session');
        }
      });
    })
    .catch((reason) => {
      console.error(`listen: fail reason=${window.Webex.Application.ErrorCodes[reason]}`);
    });
};

export default setupEventListeners;
