import styled from 'styled-components';

import StyledButtonTooltip from './StyledButtonTooltip';

const StyledButton = styled(StyledButtonTooltip)`
  background: ${(props) => props.theme.colors.button.background};
  cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  padding: 8px;
  border: none;
  border-radius: 50%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
  @media (hover: hover) {
    &:hover {
      background: ${(props) => !props.disabled && props.theme.colors.button.hover.background};
    }
  }
  &:active {
    background: ${(props) => props.theme.colors.button.hover.background};
  }
  ${({ styles }) => styles}
`;

export default StyledButton;
