import React from 'react';
import { observer } from 'mobx-react-lite';

import { useTimerStore } from '../providers/TimerStoreProvider';
import { RemainingTime } from '../styled-components';
import { getRemainingMinutes } from '../utils/helper-functions';

type Props = {
  hours: string,
  minutes: string,
  seconds: string,
};
const ActiveTimer = observer(({ hours, minutes, seconds }: Props) => {
  const timerStore = useTimerStore();
  const { timerState } = timerStore.Timer;
  const isActive = timerState === 'started' || timerState === 'paused';
  const remainingMinutes = `0${getRemainingMinutes(hours, minutes)}`.slice(-2);
  const remainingSeconds = `0${seconds}`.slice(-2);
  return (
    <RemainingTime data-testid="activated-time" active={isActive} aria-label="Remaining Time" role="timer">
      {/* When the colon is replaced with the image, add a hidden aria label */}
      <span aria-label="minutes">{remainingMinutes}</span>
      <div aria-hidden>:</div>
      <span aria-label="seconds">{remainingSeconds}</span>
    </RemainingTime>
  );
});

export default ActiveTimer;
