// @flow

import sessionStore from 'stores/SessionStore';
import trackEvent from 'services/analytics/Ahoy';

import setupSession from './SetupSession';
import initWebexSDK from './WebexSDKLoader';
import initActionCable from './ActionCableSetup';
import { configureAhoy } from './AnalyticsSetup';
import setupEventListeners from './SetupEventListeners';
import clearCookies from './ClearCookies';

const loadApplication = async () => {
  if (navigator.cookieEnabled) {
    try {
      setupSession();
      await initWebexSDK();
      await initActionCable();
      await configureAhoy();
    } catch (error) {
      console.error(error);
      return;
    }

    clearCookies();
    setupEventListeners();

    trackEvent('launch');
    trackEvent('client', { client: sessionStore.webexApp.about });
  } else {
    sessionStore.setLoading(false);
  }
};

export default loadApplication;
