// @flow
/* eslint-disable camelcase */

import { v4 as uuidv4 } from 'uuid';

import sessionStore from 'stores/SessionStore';
import apiClient from 'api/ApiClient';
import { BASE_API } from 'config';

import type { TrackedProperties } from 'models/Analytics';

export const configureAhoy = async () => {
  try {
    const visitTokensResponse = await apiClient.post('/ahoy/visits', {
      visit_token: sessionStore.meeting.conferenceId,
      visitor_token: sessionStore.user.id,
      platform: 'Web',
      landing_page: window.location.href,
      screen_width: window.screen.width,
      screen_height: window.screen.height,
      js: true,
    });

    sessionStore.setAhoyVisitTokens(visitTokensResponse.data);
  } catch (error) {
    throw new Error(error);
  }
};

export const track = (event: string, data: TrackedProperties) => {
  const analyticsData = new FormData();

  if (sessionStore.ahoyVisitTokens === undefined) {
    return; // in case analtytics is blocked
  }

  try {
    analyticsData.append('visit_token', sessionStore.ahoyVisitTokens?.visit_token);
    // $FlowIssue[incompatible-call]
    analyticsData.append('visitor_token', sessionStore.ahoyVisitTokens?.visitor_token);
    analyticsData.append(
      'events_json',
      JSON.stringify([
        {
          name: event,
          properties: data,
          time: new Date().getTime() / 1000,
          id: uuidv4(),
          js: true,
        },
      ])
    );

    window.navigator.sendBeacon(`${String(BASE_API)}/ahoy/events`, analyticsData);
  } catch (error) {
    console.error(error);
  }
};
