import React, { type ComponentType } from 'react';
import { observer } from 'mobx-react-lite';

import { TIMER_CHANGE } from 'utils/constants';

import trackEvent from 'services/analytics/Ahoy';

import sendDataToChannel from 'services/timerChannel/TimerChannel';

import { useTimerStore } from '../providers/TimerStoreProvider';
import { AddMinuteButton } from '../styled-components';
import { addMinutes } from '../utils/helper-functions';

type Props = {
  remainingDuration: number,
};

const AddMinutesView: ComponentType<Props> = observer(({ remainingDuration }: Props) => {
  const timerStore = useTimerStore();
  const { timerState, duration } = timerStore.Timer;
  const isActive = timerState === 'started' || timerState === 'paused';

  const handleAddMinute = (receivedDuration: number, amount: ?number) => {
    sendDataToChannel(TIMER_CHANGE, { duration: receivedDuration, type: 'add', amount });
    trackEvent(`add_${amount}m`);
  };

  return (
    <>
      {[15, 10, 5, 1].map((min) => (
        <AddMinuteButton
          key={`add-minute-${min}`}
          data-testid={`add-${min}-minute-btn`}
          onClick={() => handleAddMinute(addMinutes({ minutes: min, duration }), min)}
          aria-label={`Add ${min} minutes`}
          disabled={isActive && remainingDuration === 0}
        >
          +{min} min
        </AddMinuteButton>
      ))}
    </>
  );
});

export default AddMinutesView;
