import styled from 'styled-components';

type Props = {
  label: string,
};

const ImageButton = styled.button.attrs(({ label }: Props) => ({
  type: 'button',
  'aria-label': label,
}))`
  margin: ${(props) => props.margin || 'unset'};
  padding: ${(props) => props.padding || 'unset'};
  background: unset;
  border: unset;
  cursor: pointer;
`;

export default ImageButton;
