// @flow

import { NEW_USER, TIMER_CHANGE, TIMER_LOCKED, TIMER_PAUSE, TIMER_START, TIMER_TITLE_CHANGE } from './constants';

import type { Interval } from '../models/Timer';

export const numberRegex: RegExp = /^[0-9\b]+$/;

export const getCurrentProgress = (duration: number, remainingMinutes: number, remainingSeconds: number): number => {
  const totalRemainingSeconds = remainingMinutes * 60 + remainingSeconds;
  const minutes = Math.floor(duration / 60000);
  const seconds = (duration % 60000) / 1000;
  const totalDurationSeconds = Number(minutes) * 60 + Number(seconds);
  if (totalDurationSeconds === 0) {
    return 0;
  }
  return 100 - (Number(totalRemainingSeconds) * 100) / totalDurationSeconds;
};

export const getRemainingTime = (intervals: Array<Interval>, duration: number): number => {
  const alreadyExhaustedTime = intervals.reduce(
    (timerRunsFor, currentInterval) => timerRunsFor + Number(currentInterval.stop) - Number(currentInterval.start),
    0
  );
  return duration - alreadyExhaustedTime;
};

export const getExpiryTimestamp = (
  duration: number,
  intervals: Array<Interval> = [],
  startTimestamp: number = new Date().getTime(),
  offset: number | null = 0
): Date => {
  let remainingDuration = duration;
  if (intervals.length > 0) {
    remainingDuration = getRemainingTime(intervals, duration);
  }
  const time = new Date(startTimestamp);
  time.setMilliseconds(time.getMilliseconds() + remainingDuration);
  return new Date(time.getTime() - Number(offset));
};

export const getMillisecond = ({
  minutes,
  seconds,
  duration,
}: {
  minutes?: number,
  seconds?: number,
  duration: number,
}): number => {
  if (minutes != null && minutes >= 0) {
    const extractedSeconds = (duration % 60000) / 1000;
    return (minutes * 60 + extractedSeconds) * 1000;
  }
  if (seconds != null && seconds >= 0) {
    const extractedMinutes = Math.floor(duration / 60000);
    return (extractedMinutes * 60 + seconds) * 1000;
  }
  return duration;
};

export const addMinutes = ({ minutes = 0, duration = 0 }: { minutes: number, duration: number }): number => {
  const MAX_MINUTE = 99;
  if (minutes) {
    const extractedMinutes = Math.floor(duration / 60000);
    const extractedSeconds = (duration % 60000) / 1000;
    if (minutes + extractedMinutes <= 99) {
      return ((minutes + extractedMinutes) * 60 + extractedSeconds) * 1000;
    }
    return (MAX_MINUTE * 60 + extractedSeconds) * 1000;
  }
  return duration;
};

export const getRemainingMinutes = (hours: number, minutes: number): number => {
  return hours * 60 + minutes;
};

export const getRemainingSeconds = (hours: number, minutes: number, seconds: number): number => {
  return getRemainingMinutes(hours, minutes) * 60 + seconds;
};

export const isValidNumber = (value: string, maxValue: number): boolean => {
  return value !== '' && numberRegex.test(value) && Number(value) <= maxValue;
};

export const calcOffset = (serverTime: number): number => {
  return (serverTime - Math.floor(new Date().getTime() / 1000)) * 1000;
};

export const getCurrentTimeWithOffset = (offset: number | null): number => {
  return new Date().getTime() + Number(offset);
};

export const getTimeOut = (offset: number | null): number => {
  if (offset) {
    return Math.abs(offset) > 1000 ? 1400 : 1000;
  }
  return 1000;
};

export const initiatorUserName = (userId: string, initiatorUserId: string, name: string): string => {
  if (!name || (typeof name === 'string' && name.length === 0)) {
    return initiatorUserId === userId ? 'You' : 'Someone';
  }
  return initiatorUserId === userId ? 'You' : name;
};

export const isTimerUpdateRequired = (event: string, userId: string, initiatorId: string): boolean => {
  const timerUpdateEvents = [TIMER_START, TIMER_PAUSE, TIMER_LOCKED, TIMER_CHANGE, TIMER_TITLE_CHANGE];
  if (timerUpdateEvents.includes(event) || (event === NEW_USER && userId === initiatorId)) {
    return true;
  }
  return false;
};

export const isHighOffset = (offset: number): boolean => {
  return Math.abs(offset) > 1300;
};

export const shouldPlayCountDown = (
  isPlayingAudio: boolean,
  totalRemainingSeconds: number,
  isMuted: boolean,
  isClockOutOfSync: boolean
) => {
  return !isPlayingAudio && totalRemainingSeconds > 0 && totalRemainingSeconds <= 5 && !isMuted && !isClockOutOfSync;
};

export const shouldPauseCountDown = (remainingDuration: number) => {
  return remainingDuration > 5;
};

export const shouldPlayTimerEnd = (remainingDuration: number) => {
  return remainingDuration === 0;
};
