// @flow

import { debounce } from 'lodash-es';
import { observer } from 'mobx-react-lite';

import React, { useCallback, useRef, type ComponentType } from 'react';

import styled from 'styled-components';

import { useTimerStore } from 'providers/TimerStoreProvider';
import trackEvent from 'services/analytics/Ahoy';
import sendDataToChannel from 'services/timerChannel/TimerChannel';

import { TIMER_CHANGE } from 'utils/constants';
import { getMillisecond, isValidNumber } from 'utils/helper-functions';
import { ReactComponent as Up } from 'images/up.svg';
import { ReactComponent as Down } from 'images/down.svg';

import { type TimeSetEvents } from 'models/Analytics';

import { ImageButton } from '../styled-components';

const StyledInput = styled.input`
  border-bottom: 2px;
  font-size: inherit;
  background: transparent;
  color: ${(props) => props.theme.colors.text};
  font-family: 'CiscoSansTT Bold', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  width: inherit;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 2px solid transparent;
  &:focus {
    border-color: #64b4fb;
    border-radius: 8px;
    outline: none;
    background: ${(props) => props.theme.colors.focusBackground};
    color: ${(props) => props.theme.colors.activeText};
  }
  &:focus-visible {
    border-color: #64b4fb;
    border-radius: 8px;
    outline: none;
    background: ${(props) => props.theme.colors.focusBackground};
    color: ${(props) => props.theme.colors.activeText};
  }
  &::placeholder {
    color: ${(props) => props.theme.colors.text};
  }
  &:focus::placeholder {
    color: ${(props) => props.theme.colors.activeText};
  }
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  @media (max-height: 480px) {
    width: 96px;
  }

  @media (max-height: 256px) {
    width: 80px;
  }
`;

const StyledContainer = styled.div`
  font-size: inherit;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${(props) => props.theme.colors.text};
  font-family: 'CiscoSansTT Bold', 'Helvetica Neue', Helvetica, Arial, sans-serif;

  @media (max-height: 480px) {
    width: initial;
  }
`;

const InputField = styled.div`
  display: flex;
  flex-direction: column;
  width: inherit;
`;

const ArrowButton = styled(ImageButton)`
  width: 18px;

  @media (max-height: 400px) {
    display: none;
  }
`;

type Props = {
  disabled: boolean,
};

const TimerInput: ComponentType<Props> = observer(({ disabled }: Props) => {
  const timerStore = useTimerStore();
  const { duration, timerState } = timerStore.Timer;

  const minutes = `0${Math.floor(duration / 60000)}`.slice(-2);
  const seconds = `0${(duration % 60000) / 1000}`.slice(-2);

  const minutesRef = useRef<HTMLInputElement | null>(null);
  const secondsRef = useRef<HTMLInputElement | null>(null);

  const sendDebouncedDurationChange = useRef<any>(
    debounce((receivedDuration: number, eventType: TimeSetEvents) => {
      sendDataToChannel(TIMER_CHANGE, { duration: receivedDuration, type: eventType });

      // eslint-disable-next-line camelcase
      trackEvent(eventType, { initial_duration: receivedDuration });
    }, 1000)
  );

  const handleTimerChange = useCallback(
    (receivedDuration: number, eventType: TimeSetEvents) => {
      const isStopped = timerState === 'stopped' || timerState === 'cleared';

      if (isStopped) {
        // For Stopped State
        timerStore.setDuration(receivedDuration);
      }
      sendDebouncedDurationChange.current(receivedDuration, eventType);
    },
    [timerState, timerStore]
  );

  const handleMinuteChange = (e: SyntheticInputEvent<HTMLInputElement>) => {
    if (isValidNumber(e.target.value, 99)) {
      handleTimerChange(getMillisecond({ minutes: Number(e.target.value), duration }), 'set_time');
    }
  };

  const handleSecondChange = (e: SyntheticInputEvent<HTMLInputElement>) => {
    if (isValidNumber(e.target.value, 59)) {
      handleTimerChange(getMillisecond({ seconds: Number(e.target.value), duration }), 'set_time');
    }
  };

  const handleArrowClick = (type: 'add' | 'remove', field: 'minute' | 'second') => {
    if (minutesRef.current instanceof HTMLInputElement) {
      if (field === 'minute' && isValidNumber(minutesRef.current.value, 99)) {
        const currentValue = Number(minutesRef.current.value);

        if ((type === 'add' && currentValue === 99) || (type === 'remove' && currentValue === 0)) {
          return;
        }

        const newValue = type === 'add' ? currentValue + 1 : currentValue - 1;

        handleTimerChange(
          getMillisecond({ minutes: newValue, duration }),
          type === 'add' ? 'add_minutes' : 'remove_minutes'
        );
      }
    }

    if (secondsRef.current instanceof HTMLInputElement) {
      if (field === 'second' && isValidNumber(secondsRef.current.value, 59)) {
        const currentValue = Number(secondsRef.current.value);

        if ((type === 'add' && currentValue === 59) || (type === 'remove' && currentValue === 0)) {
          return;
        }

        const newValue = type === 'add' ? currentValue + 1 : currentValue - 1;

        handleTimerChange(
          getMillisecond({ seconds: newValue, duration }),
          type === 'add' ? 'add_seconds' : 'remove_seconds'
        );
      }
    }
  };

  return (
    <StyledContainer>
      <InputField>
        <ArrowButton
          label="Add Minutes"
          onClick={() => {
            handleArrowClick('add', 'minute');
          }}
          margin="0 auto 5px auto"
        >
          <Up />
        </ArrowButton>

        <StyledInput
          data-testid="minuteValue"
          type="number"
          max="99"
          min="0"
          placeholder="00"
          value={minutes}
          onChange={handleMinuteChange}
          align="end"
          aria-label="Set Minutes"
          disabled={disabled}
          ref={minutesRef}
        />

        <ArrowButton
          label="Remove Minutes"
          onClick={() => {
            handleArrowClick('remove', 'minute');
          }}
          margin="5px auto 0 auto"
        >
          <Down />
        </ArrowButton>
      </InputField>
      <div className="semi-colon" aria-hidden>
        :
      </div>
      <InputField>
        <ArrowButton
          label="Add Seconds"
          onClick={() => {
            handleArrowClick('add', 'second');
          }}
          margin="0 auto 5px auto"
        >
          <Up />
        </ArrowButton>

        <StyledInput
          data-testid="secondValue"
          type="number"
          min="0"
          placeholder="00"
          value={seconds}
          max="59"
          onChange={handleSecondChange}
          aria-label="Set Seconds"
          disabled={disabled}
          ref={secondsRef}
        />

        <ArrowButton
          label="Remove Seconds"
          onClick={() => {
            handleArrowClick('remove', 'second');
          }}
          margin="5px auto 0 auto"
        >
          <Down />
        </ArrowButton>
      </InputField>
    </StyledContainer>
  );
});

export default TimerInput;
