// @flow

import React, { useRef, type Node } from 'react';

import { observer } from 'mobx-react-lite';

import { useTimerStore } from 'providers/TimerStoreProvider';

import { CenterAlign } from 'common/typography';

import { TIMER_TITLE_CHANGE } from 'utils/constants';
import sendDataToChannel from 'services/timerChannel/TimerChannel';
import trackEvent from 'services/analytics/Ahoy';

import sessionStore from 'stores/SessionStore';

import { StyledTitle, StyledEditIcon } from '../../styled-components';

const TimerTitle: Node = observer(() => {
  const timerStore = useTimerStore();
  const { meeting } = sessionStore;
  const { title } = timerStore;
  const currentTitle = useRef(null);
  const isHost = meeting.userRoles.includes('HOST');
  const isLocked = timerStore.Timer.locked;

  const saveName = () => {
    currentTitle.current.classList.remove('blink');
    const newTitle = currentTitle.current.innerText.trim();

    if (newTitle.length > 0 && newTitle !== title) {
      sendDataToChannel(TIMER_TITLE_CHANGE, { title: newTitle });
      trackEvent('edit_title');
    } else {
      currentTitle.current.innerText = title;
    }
  };

  const handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      event.preventDefault();
      event.target.blur();
    } else if (event.keyCode !== 8 && currentTitle.current.innerText.length >= 36) {
      currentTitle.current.classList.add('blink');
      event.preventDefault();
    } else {
      currentTitle.current.classList.remove('blink');
    }
  };

  return (
    <CenterAlign>
      {isHost || !isLocked ? (
        <>
          <StyledEditIcon aria-hidden />
          <div>
            <StyledTitle
              ref={currentTitle}
              aria-label="Title"
              style={{ marginRight: 8, marginLeft: 8 }}
              role="textbox"
              tabIndex={0}
              contentEditable
              suppressContentEditableWarning
              onKeyDown={handleKeyUp}
              onBlur={saveName}
            >
              {title}
            </StyledTitle>
          </div>
        </>
      ) : (
        <StyledTitle>{title}</StyledTitle>
      )}
    </CenterAlign>
  );
});

export default TimerTitle;
