// @flow

import React from 'react';
import styled from 'styled-components';

import { ReactComponent as Error } from 'images/error.svg';
import { errors } from 'utils/constants';
import { Card, Description, Title } from 'common/typography';

type Props = {
  error: { message: string },
};

const StyledErrorIcon = styled(Error)`
  padding: 20px;
  border-radius: 50%;
  background: ${(props) => props.theme.colors.error.default.icon.background};
`;

const Section = styled.div`
  margin-top: 86px;
  margin-bottom: 15px;
`;

function ErrorPage(props): ComponentType<Props> {
  return (
    <Card>
      <Section>
        <StyledErrorIcon />
      </Section>
      <Title>Error</Title>
      <Description>{props.error?.message || errors.DEFAULT.message}</Description>
    </Card>
  );
}

export default ErrorPage;
