import React, { useCallback } from 'react';
import { observer } from 'mobx-react-lite';

import sessionStore from 'stores/SessionStore';
import trackEvent from 'services/analytics/Ahoy';
import sendDataToChannel from 'services/timerChannel/TimerChannel';

import { getCurrentTimeWithOffset } from 'utils/helper-functions';
import { TIMER_PAUSE, TIMER_RESET, TIMER_START } from 'utils/constants';
import { useTimerStore } from 'providers/TimerStoreProvider';

import { getClearStatus, getPlayPauseStatus, getResetStatus } from 'utils/controls';

import {
  PlayPauseButton,
  ControlButton,
  StyledPaused,
  StyledPlayIcon,
  StyledClearIcon,
  StyledResetIcon,
} from '../../styled-components';

const TimerControls = observer(() => {
  const timerStore = useTimerStore();
  const { meeting } = sessionStore;
  const { duration, timerState, locked } = timerStore.Timer;
  const isRunning = timerState === 'started';
  const isHost = meeting.userRoles.includes('HOST');
  const isPlayPauseDisabled = getPlayPauseStatus(locked, isHost, timerState);
  const isClearDisabled = getClearStatus(locked, isHost, timerState);
  const isResetDisabled = getResetStatus(locked, isHost, timerState);
  const { offset } = sessionStore;

  const handleStart = useCallback(() => {
    if (duration > 0) {
      /* eslint-disable camelcase */
      sendDataToChannel(TIMER_START, { start_time: getCurrentTimeWithOffset(offset) });
      trackEvent('start_timer');
    }
  }, [duration, offset]);

  const handlePause = useCallback(() => {
    /* eslint-disable camelcase */
    sendDataToChannel(TIMER_PAUSE, { pause_time: getCurrentTimeWithOffset(offset) });
    trackEvent('pause_timer');
  }, [offset]);

  const handleReset = useCallback((type: string) => {
    if (type === 'initial') {
      sendDataToChannel(TIMER_RESET, { type });
      trackEvent('reset');
    } else if (type === 'zero') {
      sendDataToChannel(TIMER_RESET, { type: 'zero' });
      trackEvent('clear');
    }
  }, []);

  return (
    <>
      <PlayPauseButton
        data-testid="timer-control-btn"
        onClick={() => (isRunning ? handlePause() : handleStart())}
        disabled={isPlayPauseDisabled}
        aria-label={`${isRunning ? 'Pause Timer' : 'Start Timer'}`}
      >
        {isRunning ? (
          <StyledPaused disabled={isPlayPauseDisabled} />
        ) : (
          <StyledPlayIcon disabled={isPlayPauseDisabled} />
        )}
        <span className="tooltiptext" aria-hidden>
          {isRunning ? 'Pause' : 'Start'}
        </span>
      </PlayPauseButton>
      <ControlButton
        data-testid="clear-btn"
        onClick={() => handleReset('zero')}
        disabled={isClearDisabled}
        aria-label="Clear Timer"
      >
        <StyledClearIcon disabled={isClearDisabled} />
        <span className="tooltiptext" aria-hidden>
          Clear
        </span>
      </ControlButton>
      <ControlButton
        data-testid="reset-btn"
        onClick={() => handleReset('initial')}
        aria-label="Reset Timer"
        disabled={isResetDisabled}
      >
        <StyledResetIcon disabled={isResetDisabled} />
        <span className="tooltiptext" aria-hidden>
          Reset
        </span>
      </ControlButton>
    </>
  );
});

export default TimerControls;
