import React, { useCallback } from 'react';
import { observer } from 'mobx-react-lite';

import trackEvent from 'services/analytics/Ahoy';

import { ControlButton, StyledMute } from '../../styled-components';
import { ReactComponent as Notification } from '../../images/notification.svg';
import { useTimerStore } from '../../providers/TimerStoreProvider';

type Props = {
  pauseAudio: () => void,
  isPlayingAudio: boolean,
};

const AudioControls = observer(({ pauseAudio, isPlayingAudio }: Props) => {
  const timerStore = useTimerStore();
  const { muted: isMuted } = timerStore.Timer;

  const handleMute = useCallback(
    (muteStatus: boolean) => {
      timerStore.setMuted(muteStatus);
      if (muteStatus && isPlayingAudio) {
        pauseAudio();
      }
      trackEvent(`${muteStatus ? 'mute' : 'unmute'}`);
    },
    [isPlayingAudio, pauseAudio, timerStore]
  );

  if (isMuted) {
    return (
      <ControlButton data-testid="unmute-btn" aria-label="Unmute" onClick={() => handleMute(false)}>
        <StyledMute />
        <span className="tooltiptext" aria-hidden>
          Unmute
        </span>
      </ControlButton>
    );
  }

  return (
    <ControlButton data-testid="mute-btn" aria-label="Mute" onClick={() => handleMute(true)}>
      <Notification />
      <span className="tooltiptext" aria-hidden>
        Mute
      </span>
    </ControlButton>
  );
});

export default AudioControls;
