// @flow

import { FRONTEND_URL } from 'config';
import sessionStore from 'stores/SessionStore';
import { getSessionUuid } from 'utils/uuid';

// ToDo: this function is more a util function than a loader, move it to a util folder
const shareAppUrl = async () => {
  if (sessionStore.webexApp.isShared === false) {
    try {
      const sharedUrl = `${String(FRONTEND_URL)}/${getSessionUuid()}`;
      await sessionStore.webexApp.setShareUrl(sharedUrl, '', 'Shared Timer');
    } catch (reason) {
      console.error(`Set share url failed; reason=${window.Webex.Application.ErrorCodes[reason]}`);
    }
  }
};

export default shareAppUrl;
