import { useCallback, useState } from 'react';

const useTimerAudio = () => {
  const [isPlayingAudio, setIsPlayingAudio] = useState(false);
  const timerCountDown = ((document.getElementById('timer-countdown'): any): HTMLMediaElement);
  const timerEnd = ((document.getElementById('timer-end'): any): HTMLMediaElement);

  const pauseCountDownAudio = useCallback(async () => {
    if (timerCountDown) {
      await timerCountDown.pause();
      timerCountDown.currentTime = 0;
    }
    setIsPlayingAudio(false);
  }, [timerCountDown]);

  const playCountDownAudio = () => {
    timerCountDown.play();
    setIsPlayingAudio(true);
  };

  const playTimerEndAudio = () => {
    timerEnd.play();
    setIsPlayingAudio(true);
  };

  const resetAudio = useCallback(async () => {
    if (timerCountDown && timerEnd) {
      await timerCountDown?.pause();
      await timerEnd?.pause();
      timerCountDown.currentTime = 0;
      timerEnd.currentTime = 0;
    }
    setIsPlayingAudio(false);
  }, [timerCountDown, timerEnd]);

  return {
    isPlayingAudio,
    pauseCountDownAudio,
    playCountDownAudio,
    playTimerEndAudio,
    resetAudio,
  };
};

export default useTimerAudio;
