import { cloneDeep } from 'lodash-es';
import { useCallback } from 'react';
import { useTimer } from 'react-timer-hook';

import { getCurrentTimeWithOffset, getExpiryTimestamp } from '../utils/helper-functions';

const useTimerWrapper = () => {
  const {
    seconds: remainingSeconds,
    minutes: remainingMinutes,
    hours: remainingHours,
    pause,
    restart,
  } = useTimer({
    expiryTimestamp: new Date(getExpiryTimestamp(300000)),
    autoStart: false,
  });

  const handleTimerStart = useCallback(
    (intervals: Array<Interval>, duration: number, offset: number) => {
      const lastInterval = intervals.splice(-1)[0].start;
      restart(new Date(getExpiryTimestamp(duration, intervals, lastInterval, offset)), true);
    },
    [restart]
  );

  const handleTimerPause = useCallback(
    (intervals: Array<Interval>, duration: number) => {
      pause();
      restart(new Date(getExpiryTimestamp(duration, intervals)), false);
    },
    [pause, restart]
  );

  const handleTimerReset = useCallback(
    async (duration: number) => {
      restart(new Date(getExpiryTimestamp(duration)), false);
    },
    [restart]
  );

  const handleTimerWithOffset = (duration: number, intervals: Array<Interval>, offset: number) => {
    const currentIntervals = cloneDeep(intervals);
    const lastInterval = currentIntervals.length ? currentIntervals.splice(-1)[0].start : getCurrentTimeWithOffset(0);
    restart(new Date(getExpiryTimestamp(duration, currentIntervals, lastInterval, offset)), true);
  };

  return {
    remainingHours,
    remainingMinutes,
    remainingSeconds,
    reset: handleTimerReset,
    start: handleTimerStart,
    pause: handleTimerPause,
    handleTimerWithOffset,
  };
};

export default useTimerWrapper;
