// @flow

import React, { type ComponentType } from 'react';
import { observer } from 'mobx-react-lite';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createGlobalStyle } from 'styled-components';
import FadeLoader from 'react-spinners/FadeLoader';

import ErrorPage from 'components/Error/Error';

import { errors } from 'utils/constants';

import sessionStore from './stores/SessionStore';
import { SessionStoreProvider } from './providers/SessionStoreProvider';
import { TimerStoreProvider } from './providers/TimerStoreProvider';

import loadApplication from './services/app-loader/AppLoader';

import themes from './theme/schema.json';
import TimerMaster from './components/TimerMaster';
// import Feedback from './component/Feedback/Feedback';

import './App.css';

const GlobalTheme = createGlobalStyle`
  body {
    background: ${(props) => props.theme.colors.body};
    font-family: 'CiscoSansTT Regular', sans;
  }
`;

loadApplication();

const selectEntryPoint = () => {
  return navigator.cookieEnabled ? <TimerMaster /> : <ErrorPage error={errors.COOKIES_DISABLED} />;
};

const App: ComponentType<any> = observer(() => {
  return (
    <ThemeProvider theme={themes.data[sessionStore.theme]}>
      <GlobalTheme />
      <div className="App">
        {sessionStore.isLoading ? (
          <div className="loader">
            <FadeLoader loading height={10} width={5} radius={2} />
          </div>
        ) : (
          <SessionStoreProvider>
            <TimerStoreProvider>
              <BrowserRouter>
                <Routes>
                  <Route path="/" element={selectEntryPoint()}>
                    <Route path=":sessionId" element={<TimerMaster />} />
                  </Route>
                  {/* <Route path="/feedback" element={<Feedback />} /> */}
                  <Route path="/error" element={<ErrorPage />} />
                </Routes>
              </BrowserRouter>
            </TimerStoreProvider>
          </SessionStoreProvider>
        )}
      </div>
    </ThemeProvider>
  );
});

export default App;
