// @flow

import sessionStore from 'stores/SessionStore';
import { getSessionUuid } from 'utils/uuid';

import { track } from '../app-loader/AnalyticsSetup';

import type { TrackedEventName, AdditionalProperties } from 'models/Analytics';

const trackEvent = (eventName: TrackedEventName, additionalData?: AdditionalProperties = {}) => {
  track(`timerapp_${eventName}`, {
    conferenceId: sessionStore.meeting.conferenceId,
    roles: sessionStore.meeting.userRoles,
    userId: sessionStore.user.id,
    orgId: sessionStore.user.orgId,
    sessionId: getSessionUuid(),
    ...additionalData,
  });
};

export default trackEvent;
