// @flow

export const getLockTooltip = (isLocked: boolean, isHost: boolean) => {
  if (isLocked) {
    if (isHost) {
      return 'Share control';
    }
    return 'Locked by host';
  }
  if (isHost) {
    return 'Lock control';
  }
  return 'Unlocked by host';
};

export const getPlayPauseStatus = (isLocked: boolean, isHost: boolean, timerState: string) => {
  if (isLocked) {
    if (isHost) {
      return timerState === 'cleared';
    }
    return true;
  }
  return timerState === 'cleared';
};

export const getClearStatus = (isLocked: boolean, isHost: boolean, timerState: string) => {
  if (isLocked) {
    if (isHost) {
      return ['started', 'cleared'].includes(timerState);
    }
    return true;
  }
  return ['started', 'cleared'].includes(timerState);
};

export const getResetStatus = (isLocked: boolean, isHost: boolean, timerState: string) => {
  if (isLocked) {
    if (isHost) {
      return ['stopped', 'cleared'].includes(timerState);
    }
    return true;
  }
  return ['stopped', 'cleared'].includes(timerState);
};
