// @flow

import { makeAutoObservable, reaction } from 'mobx';
import { cloneDeep } from 'lodash-es';

import { isHighOffset } from 'utils/helper-functions';

import { getToastFromMessage } from 'utils/toasts';

import sessionStore from './SessionStore';

import type { Timer, Interval, TimerState, Feed } from '../models/Timer';

class TimerStore {
  // observables
  timer: Timer = {
    intervals: [],
    duration: 300000,
    muted: false,
    locked: false,
    timerState: 'stopped', // timer_state on server
  };

  title: string = 'Shared Timer';
  activityToasts: Array<Feed> = [];

  constructor() {
    makeAutoObservable(this);
    reaction(
      () => sessionStore.offset,
      (offset) => {
        this.handleOffset(offset);
      }
    );
  }

  // computed
  get Timer(): Timer {
    return this.timer;
  }

  // actions
  setTimer(timerData: Timer) {
    this.timer = { ...this.timer, timerData };
  }

  // actions
  setLocked(locked: boolean) {
    this.timer.locked = locked;
  }

  // actions
  setDuration(duration: number) {
    this.timer.duration = duration;
  }

  // actions
  setTimerIntervals(intervals: Array<Interval>) {
    this.timer.intervals = intervals;
  }

  // actions
  setMuted(muted: boolean) {
    this.timer.muted = muted;
  }

  // actions
  setTimerState(timerState: TimerState) {
    this.timer.timerState = timerState;
  }

  // actions
  setActivityToasts(toast: Feed) {
    if (toast.msg === null) {
      return;
    }

    const updatedToast = [toast, ...this.activityToasts];
    this.activityToasts = updatedToast.slice(0, 3);
  }

  // actions
  setTitle(title: string) {
    this.title = title;
  }

  async handleConsumerReset(timerData: TimerBroadcastData) {
    this.setDuration(timerData.duration);
    this.setTimerState(timerData.timer_state);
    this.setTimerIntervals(cloneDeep(timerData.intervals));
    this.timerHandler.reset(timerData.duration);
    this.audioHandler.resetAudio();
  }

  handleConsumerStart(intervals: Array<Interval>, receivedDuration: number, timerStatus: TimerState) {
    this.setTimerIntervals(cloneDeep(intervals));
    this.setTimerState(timerStatus);
    this.timerHandler.start(intervals, receivedDuration, sessionStore.offset);
  }

  handleConsumerPause(intervals: Array<Interval>, receivedDuration: number, timerStatus: TimerState) {
    this.setTimerState(timerStatus);
    this.setTimerIntervals(cloneDeep(intervals));
    this.audioHandler.pauseCountDownAudio();
    this.timerHandler.pause(intervals, receivedDuration);
  }

  updateTimer(
    intervals: Array<Interval>,
    receivedDuration: number,
    receivedLockStatus: boolean,
    timerStatus: TimerState,
    title: string
  ) {
    this.setLocked(receivedLockStatus);
    this.setDuration(receivedDuration);
    this.setTitle(title);
    if (timerStatus === 'started') {
      this.handleConsumerStart(intervals, receivedDuration, timerStatus);
    } else {
      this.handleConsumerPause(intervals, receivedDuration, timerStatus);
    }
  }

  handleOffset(offset: number) {
    const { isClockOutOfSync } = sessionStore;
    const { duration, intervals, timerState } = this.Timer;
    if (offset === null) {
      return;
    }
    if (isClockOutOfSync) {
      console.warn('Out of sync');
      return;
    }
    if (isHighOffset(offset) && timerState === 'started') {
      this.setActivityToasts(getToastFromMessage('Poor network connection'));
      console.warn(`High Offset time ${offset} ms`);
    }
    if (timerState === 'started') {
      this.timerHandler.handleTimerWithOffset(duration, intervals, offset);
      this.setActivityToasts(getToastFromMessage('Resynced with time server'));
    }
  }
}

export default TimerStore;
