// @flow

import { makeAutoObservable } from 'mobx';

import type { Subscription } from '@rails/actioncable';
import type { WebexApplication, DisplayContext } from 'models/WebexSDK';
import type { User } from 'models/User';
import type { Meeting, Role } from 'models/Meeting';
import type { TimerChannel } from 'models/ActionCable';
import type { Visit } from 'models/Analytics';

class SessionStore {
  // observables
  webexApp: WebexApplication;
  user: User;
  meeting: Meeting;
  displayContext: DisplayContext = 'MEETING_SIDEBAR';
  theme: 'LIGHT' | 'DARK' = 'DARK';
  timerChannel: Subscription<TimerChannel>;
  offset: number = 0;
  isCableConnected: boolean = true;
  isLoading: boolean = true;
  ahoyVisitTokens: void | Visit;

  constructor() {
    makeAutoObservable(this);
  }

  // computed
  get isClockOutOfSync(): boolean {
    return Math.abs(this.offset) >= 60000;
  }

  // actions
  setWebexApp(webexApp: WebexApplication) {
    this.webexApp = webexApp;
  }

  setUser(user: User) {
    this.user = user;
  }

  setMeeting(meeting: Meeting) {
    this.meeting = meeting;
  }

  setDisplayContext(displayContext: DisplayContext) {
    this.displayContext = displayContext;
  }

  setTheme(theme: 'LIGHT' | 'DARK') {
    this.theme = theme;
  }

  setTimerChannel(channel: Subscription<TimerChannel>) {
    this.timerChannel = channel;
  }

  setOffset(offset: number) {
    this.offset = offset;
  }

  setIsCableConnected(isConnected: boolean) {
    this.isCableConnected = isConnected;
  }

  setLoading(state: boolean) {
    this.isLoading = state;
  }

  setRoles(roles: Array<Role>) {
    this.meeting.userRoles = roles;
  }

  setAhoyVisitTokens(visitTokens: Visit) {
    this.ahoyVisitTokens = visitTokens;
  }
}

const sessionStore: SessionStore = new SessionStore();
export default sessionStore;
