// @flow

import { v4 as uuidv4 } from 'uuid';

// Don't use this function to create UUIDs with uniqueness guarantees, use the uuid module instead.
// Use this function to create keys for arrays that describe React components
export function generateReactKey(): string {
  // eslint-disable-next-line func-names
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    // eslint-disable-next-line no-bitwise
    const r = (Math.random() * 16) | 0;
    // eslint-disable-next-line no-bitwise
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export const getSessionUuid = (): string => {
  return window.sessionStorage.getItem('session');
};

export const setSessionUuid = (uuid: string) => {
  window.sessionStorage.setItem('session', uuid);
};

export const createSessionUuid = () => {
  const sessionUuid = uuidv4();

  setSessionUuid(sessionUuid);
};

export const extractUuidFromPathname = (pathname: string): string => {
  // remove the "/" from the pathname
  return pathname.replace(/\//, '');
};
