// @flow

import React, { type Node } from 'react';

import { ScreenReaderText } from 'common/typography';

import {
  ACTION_BLOCKED,
  TIMER_CHANGE,
  TIMER_LOCKED,
  TIMER_PAUSE,
  TIMER_RESET,
  TIMER_START,
  TIMER_TITLE_CHANGE,
} from './constants';
import { initiatorUserName } from './helper-functions';

import { generateReactKey } from './uuid';
import { getMinutesFromDuration, getSecondsFromDuration } from './time';

import type { Feed, TimerBroadcastData } from '../models/Timer';

const newDurationCallout = (duration: number): string => {
  const minutes = getMinutesFromDuration(duration);
  const seconds = getSecondsFromDuration(duration);
  let minutesString = '';
  let secondsString = '';

  if (minutes === 0 && seconds === 0) {
    return '';
  }

  if (minutes > 0) {
    minutesString = `${minutes} ${minutes === 1 ? 'minute' : 'minutes'}`;
  }

  if (seconds > 0) {
    secondsString = `, ${seconds} ${seconds === 1 ? 'second' : 'seconds'}`;
  }

  return `New duration: ${minutesString}${secondsString}`;
};

export const getActionBlockedFeed = (rejectedEvent: string): Node => {
  if (rejectedEvent === TIMER_START) {
    return (
      <>
        Unable to <b>start</b> timer
      </>
    );
  }
  if (rejectedEvent === TIMER_PAUSE) {
    return (
      <>
        Unable to <b>pause</b> timer
      </>
    );
  }
  return null;
};

export const getActivityMessage = (userId: string, data: TimerBroadcastData): Node => {
  const { event, user_id: initiatorUserId, user_name: userName, type, amount, locked, source, duration, title } = data;
  switch (event) {
    case TIMER_RESET: {
      if (type === 'zero') {
        return (
          <>
            {initiatorUserName(userId, initiatorUserId, userName)} <b>cleared</b> the timer
          </>
        );
      }
      return (
        <>
          {initiatorUserName(userId, initiatorUserId, userName)} <b>reset</b> the timer
          <ScreenReaderText>{newDurationCallout(duration)}</ScreenReaderText>
        </>
      );
    }
    case TIMER_START: {
      return (
        <>
          {initiatorUserName(userId, initiatorUserId, userName)} <b>started</b> the timer
        </>
      );
    }
    case TIMER_PAUSE: {
      return (
        <>
          {initiatorUserName(userId, initiatorUserId, userName)} <b>paused</b> the timer
        </>
      );
    }
    case TIMER_CHANGE: {
      if (type === 'add') {
        return (
          <>
            {initiatorUserName(userId, initiatorUserId, userName)} <b>added</b> {amount}{' '}
            {Number(amount) === 1 ? 'minute' : 'minutes'}
            <ScreenReaderText>{newDurationCallout(duration)}</ScreenReaderText>
          </>
        );
      }
      if (type.includes('add_')) {
        return (
          <>
            {initiatorUserName(userId, initiatorUserId, userName)} <b>added</b> time
            <ScreenReaderText>{newDurationCallout(duration)}</ScreenReaderText>
          </>
        );
      }
      if (type.includes('remove_')) {
        return (
          <>
            {initiatorUserName(userId, initiatorUserId, userName)} <b>removed</b> time
            <ScreenReaderText>{newDurationCallout(duration)}</ScreenReaderText>
          </>
        );
      }
      return (
        <>
          {initiatorUserName(userId, initiatorUserId, userName)} <b>changed</b> the timer duration
          <ScreenReaderText>{newDurationCallout(duration)}</ScreenReaderText>
        </>
      );
    }
    case TIMER_LOCKED: {
      return (
        <>
          {/* eslint-disable-next-line max-len */}
          {initiatorUserName(userId, initiatorUserId, userName)} <b>{locked ? 'locked' : 'unlocked'}</b> the timer
        </>
      );
    }
    case TIMER_TITLE_CHANGE: {
      return (
        <>
          {initiatorUserName(userId, initiatorUserId, userName)} <b>updated</b> the timer title to {title}
        </>
      );
    }
    case ACTION_BLOCKED: {
      return getActionBlockedFeed(source);
    }
    default: {
      return null;
    }
  }
};

export const getActivityToast = (userId: string, data: TimerBroadcastData): Feed => {
  return {
    msg: getActivityMessage(userId, data),
    key: generateReactKey(),
  };
};

export const getToastFromMessage = (feed: string): Feed => {
  return { msg: feed, key: generateReactKey() };
};

export const isToastUpdateRequired = (event: string): boolean => {
  const toastRequiredEvents = [TIMER_START, TIMER_PAUSE, TIMER_LOCKED, TIMER_CHANGE, TIMER_RESET, TIMER_TITLE_CHANGE];
  if (toastRequiredEvents.includes(event)) {
    return true;
  }
  return false;
};
