import React from 'react';
import { observer } from 'mobx-react-lite';

import { useTimerStore } from '../providers/TimerStoreProvider';
import { ProgressBar } from '../styled-components';
import { getCurrentProgress, getRemainingMinutes } from '../utils/helper-functions';

type Props = {
  hours: number,
  minutes: number,
  seconds: number,
};

const ProgressBarView: ComponentType<Props> = observer(
  ({ hours: remainingHours, minutes: remainingMinutes, seconds: remainingSeconds }: Props) => {
    const timerStore = useTimerStore();
    const { timerState, duration } = timerStore.Timer;
    const isStopped = timerState === 'stopped' || timerState === 'cleared';
    const isPaused = timerState === 'paused';
    return (
      <ProgressBar
        data-testid="progress-bar"
        paused={isPaused}
        value={
          !isStopped
            ? getCurrentProgress(duration, getRemainingMinutes(remainingHours, remainingMinutes), remainingSeconds)
            : 0
        }
        min={0}
        max={100}
        aria-hidden
      />
    );
  }
);

export default ProgressBarView;
