import { observer } from 'mobx-react-lite';
import React, { useCallback } from 'react';

import trackEvent from 'services/analytics/Ahoy';

import { useTimerStore } from 'providers/TimerStoreProvider';
import sessionStore from 'stores/SessionStore';
import { TIMER_LOCKED } from 'utils/constants';
import { getLockTooltip } from 'utils/controls';

import sendDataToChannel from 'services/timerChannel/TimerChannel';

import { ControlButton, StyledLock, StyledUnlock } from '../../styled-components';

const LockControls = observer(() => {
  const timerStore = useTimerStore();
  const { meeting } = sessionStore;
  const { locked: isLocked } = timerStore.Timer;
  const isHost = meeting.userRoles.includes('HOST');

  const handleLock = useCallback((lockStatus: boolean) => {
    sendDataToChannel(TIMER_LOCKED, { locked: lockStatus });
    trackEvent(`${lockStatus ? 'lock_timer' : 'unlock_timer'}`);
  }, []);

  return (
    <ControlButton
      data-testid="lock-control-btn"
      onClick={() => handleLock(!isLocked)}
      aria-label={`${isLocked ? 'Share Control' : 'Lock Control'}`}
      disabled={!isHost}
      showTooltip
    >
      {isLocked ? <StyledLock disabled={!isHost} /> : <StyledUnlock disabled={!isHost} />}
      <span className="tooltiptext" aria-hidden>
        {getLockTooltip(isLocked, isHost)}
      </span>
    </ControlButton>
  );
});

export default LockControls;
