import React, { type Node } from 'react';
import styled from 'styled-components';

const Props = {
  poorNetwork: Node,
  overflowSwitch: Node,
  title: Node,
  content: Node,
  addSubtractMinutes: Node,
  progress: Node,
  feed: Node,
  footer: Node,
};

const VerticalFlexLayout = styled.div`
  height: 100%;
  padding: 16px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-height: 200px) {
    padding: 2px 0;
  }
`;

const HeaderContainer = styled.div`
  height: 22px;
  padding-bottom: 16px;
  display: flex;
  justify-content: space-between;

  @media (max-height: 496px) {
    height: 0;
    padding: 0;
    opacity: 0;
  }

  @media (max-height: 216px) {
    display: none;
  }
`;

const TitleContainer = styled.div`
  margin-bottom: 16px;

  @media (max-height: 304px) {
    display: none;
  }
`;

const ContentContainer = styled.div`
  font-size: 98px;

  @media (max-height: 480px) {
    // width parameters are configured in the timer text component
    font-size: 62px;
  }

  @media (max-height: 256px) {
    // width parameters are configured in the timer text component
    font-size: 48px;
  }
`;

const ProgressContainer = styled.div`
  @media (max-height: 256px) {
    position: relative;
    top: -6px;
  }
`;

const AddSubtractContainer = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 15px;

  @media (max-height: 256px) {
    margin-top: 5px;
  }
`;

const FeedContainer = styled.div`
  @media (max-height: 376px) {
    display: none;
  }
`;

const FooterContainer = styled.div`
  height: 52px;
  padding-top: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-height: 256px) {
    padding-top: 8px;
  }

  @media (max-height: 216px) {
    height: 36px;
    padding-top: 4px;
  }
`;

function TimerLayout({
  poorNetwork,
  overflowSwitch,
  title,
  content,
  addSubtractMinutes,
  progress,
  feed,
  footer,
}: Props) {
  return (
    <VerticalFlexLayout>
      <HeaderContainer>
        <div>{poorNetwork}</div>
        <div>{overflowSwitch}</div>
      </HeaderContainer>
      <div>
        <TitleContainer>{title}</TitleContainer>
        <ContentContainer>{content}</ContentContainer>
        <ProgressContainer>{progress}</ProgressContainer>
        <AddSubtractContainer>{addSubtractMinutes}</AddSubtractContainer>
        <FeedContainer>{feed}</FeedContainer>
      </div>
      <FooterContainer>{footer}</FooterContainer>
    </VerticalFlexLayout>
  );
}

export default TimerLayout;
