// @flow

import React, { type Node } from 'react';
import styled from 'styled-components';

import { type Feed } from 'models/Timer';

type Props = {
  activities: Array<Feed>,
};

const Wrapper = styled.div`
  max-width: 330px;
  font-size: 14px;
  text-align: center;
  height: 56px;
  overflow: hidden;
  margin-top: 15px;
`;

const ActivityDescription = styled.div`
  color: ${(props) => props.theme.colors.activeText};
  user-select: none;

  &:nth-child(2) {
    opacity: 0.7;
  }

  &:nth-child(3) {
    opacity: 0.35;
  }
`;

function ActivityToasts({ activities = [] }: Props): Node {
  return (
    <Wrapper role="feed">
      {activities.map((activity) => (
        <ActivityDescription key={activity.key} aria-live="polite">
          {activity.msg}
        </ActivityDescription>
      ))}
    </Wrapper>
  );
}

export default ActivityToasts;
