// @flow

import { createConsumer } from '@rails/actioncable';

import { BASE_API } from 'config';
import sessionStore from 'stores/SessionStore';
import apiClient from 'api/ApiClient';
import { calcOffset } from 'utils/cable';
import { getSessionUuid } from 'utils/uuid';

import shareAppUrl from './ShareAppUrl';

const initActionCable = async () => {
  if (!sessionStore.webexApp) {
    throw new Error('Webex App is not set up');
    // may need to change this error
  }

  let socket: WebSocket;
  const socketPinglistener = (event: MessageEvent) => {
    const response = event.data;
    const msg = JSON.parse(String(response));

    if (msg.type === 'ping') {
      sessionStore.setOffset(calcOffset(msg.message));
    }
  };

  try {
    const tokenResponse = await apiClient.post('/api/v1/launches', {
      meeting: sessionStore.meeting,
      user: sessionStore.user,
      sessionId: getSessionUuid(),
    });

    const token: string = tokenResponse.data.jwt;
    const consumer = await createConsumer(`${String(BASE_API)}/cable?token=${token}`);

    const timerChannel = await consumer.subscriptions.create(
      {
        channel: 'TimerChannel',
      },
      {
        connected: () => {
          sessionStore.setTimerChannel(timerChannel);
          sessionStore.setIsCableConnected(true);

          socket = timerChannel?.consumer?.connection?.monitor?.connection?.webSocket;

          if (socket?.url.length > 0) {
            socket.addEventListener('message', socketPinglistener);
          }
          sessionStore.setLoading(false);
          timerChannel.send({
            event: 'NEW_USER',
          });
          shareAppUrl();
        },
        disconnected: () => {
          if (socket?.url.length > 0) {
            console.warn('disconnected');
            sessionStore.setIsCableConnected(false);
            socket.removeEventListener('message', socketPinglistener);
          }
        },
      }
    );
  } catch (error) {
    throw new Error(error);
  }
};

export default initActionCable;
