// @flow

import React, { useCallback, useEffect, type ComponentType } from 'react';
import { observer } from 'mobx-react-lite';
import styled from 'styled-components';

import sessionStore from 'stores/SessionStore';

import trackEvent from 'services/analytics/Ahoy';

import { isToastUpdateRequired, getActivityToast } from 'utils/toasts';
import {
  getCurrentTimeWithOffset,
  getRemainingSeconds,
  getTimeOut,
  isTimerUpdateRequired,
  shouldPlayCountDown,
  shouldPauseCountDown,
  shouldPlayTimerEnd,
} from 'utils/helper-functions';
import { ACTION_BLOCKED, TIMER_RESET, TIMER_STOP } from 'utils/constants';

import sendDataToChannel from 'services/timerChannel/TimerChannel';

import { useTimerStore } from 'providers/TimerStoreProvider';

import { ReactComponent as WarningIcon } from 'images/warning.svg';
import { CenterAlign } from 'common/typography';

import { TimerView, PoorConnection } from '../styled-components';

import TimerLayout from './Layout/TimerLayout';

import './TimerMaster.css';

import TimerInput from './TimerInput';
import ActivityToasts from './ActivityToasts';
import OutOfSync from './out-of-sync/OutOfSync';
import ProgressBarView from './ProgressBarView';
import TimerControls from './Controls/TimerControls';
import AudioControls from './Controls/AudioControls';
import LockControls from './Controls/LockControls';
import AddMinutesView from './AddMinutes';
import useTimerAudio from './useTimerAudio';
import ActiveTimer from './ActiveTimer';

import useTimerWrapper from './useTimerWrapper';

import TimerTitle from './Title/TimerTitle';

import type { TimerBroadcast } from 'models/Timer';

const ProgressSpacer = styled.div`
  @media (max-height: 400px) {
    height: 22px;
  }
`;

const TimerMaster: ComponentType<any> = observer(() => {
  const timerStore = useTimerStore();
  const { remainingHours, remainingMinutes, remainingSeconds, handleTimerWithOffset, start, reset, pause } =
    useTimerWrapper();
  timerStore.timerHandler = { start, reset, pause, handleTimerWithOffset };
  const { locked: isLocked, muted: isMuted, timerState } = timerStore.Timer;
  const { meeting, offset, isCableConnected, user: { id: userId } = {}, isClockOutOfSync } = sessionStore;
  const remainingDuration = getRemainingSeconds(remainingHours, remainingMinutes, remainingSeconds);
  const { isPlayingAudio, pauseCountDownAudio, playCountDownAudio, playTimerEndAudio, resetAudio } = useTimerAudio();
  timerStore.audioHandler = { pauseCountDownAudio, resetAudio };

  useEffect(() => {
    const isRunning = timerState === 'started';
    if (isRunning) {
      if (shouldPauseCountDown(remainingDuration)) {
        pauseCountDownAudio();
      }
      if (shouldPlayCountDown(isPlayingAudio, remainingDuration, isMuted, isClockOutOfSync)) {
        playCountDownAudio();
      }
      if (shouldPlayTimerEnd(remainingDuration)) {
        if (!isMuted && !isClockOutOfSync) {
          pauseCountDownAudio();
          playTimerEndAudio();
        }

        const timeOut = getTimeOut(offset);
        if (!isClockOutOfSync) {
          setTimeout(() => {
            sendDataToChannel(TIMER_STOP, { type: 'natural', time: getCurrentTimeWithOffset(offset) });
          }, timeOut);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isMuted, timerState, offset, isClockOutOfSync, remainingDuration]);

  const onReceiving = useCallback(
    (response: TimerBroadcast) => {
      const {
        event,
        user_id: initiatorId,
        intervals,
        duration: receivedDuration,
        timer_state: timerStatus,
        locked: receivedLockStatus,
        type,
        title,
      } = response.data;
      switch (event) {
        case ACTION_BLOCKED: {
          if (userId === initiatorId) {
            timerStore.setActivityToasts(getActivityToast(userId, response.data));
            trackEvent('timer_action_rejected');
          }
          break;
        }
        case TIMER_STOP: {
          timerStore.handleConsumerReset(response.data);
          break;
        }
        case TIMER_RESET: {
          timerStore.handleConsumerReset(response.data);
          if (type === 'zero') {
            timerStore.updateTimer(intervals, receivedDuration, receivedLockStatus, timerStatus, title);
          }
          break;
        }
        default:
          if (isTimerUpdateRequired(event, userId, initiatorId)) {
            timerStore.updateTimer(intervals, receivedDuration, receivedLockStatus, timerStatus, title);
          }
          break;
      }

      if (isToastUpdateRequired(event)) {
        timerStore.setActivityToasts(getActivityToast(userId, response.data));
      }
    },
    [userId, timerStore]
  );

  sessionStore.timerChannel.received = (response) => onReceiving(response);
  const isHost = meeting.userRoles.includes('HOST');
  const isActive = timerState === 'started' || timerState === 'paused';

  if (isClockOutOfSync) {
    return <OutOfSync />;
  }
  return (
    <TimerLayout
      poorNetwork={
        !isCableConnected ? (
          <CenterAlign>
            <PoorConnection>
              <WarningIcon aria-hidden />
              Poor Connection
            </PoorConnection>
          </CenterAlign>
        ) : null
      }
      overflowSwitch={null} // For now
      title={<TimerTitle />}
      content={
        <TimerView>
          {isActive || (!isHost && isLocked) ? (
            <ActiveTimer hours={remainingHours} minutes={remainingMinutes} seconds={remainingSeconds} />
          ) : (
            <TimerInput disabled={isClockOutOfSync} />
          )}
        </TimerView>
      }
      addSubtractMinutes={!isLocked || isHost ? <AddMinutesView remainingDuration={remainingDuration} /> : null}
      progress={
        isActive ? (
          <ProgressBarView hours={remainingHours} minutes={remainingMinutes} seconds={remainingSeconds} />
        ) : (
          <ProgressSpacer />
        )
      }
      feed={<ActivityToasts activities={timerStore.activityToasts} />}
      footer={
        <div className="footer-content">
          <div className="controls">
            <LockControls />
            <AudioControls pauseAudio={pauseCountDownAudio} isPlayingAudio />
          </div>
          <div className="controls">
            <TimerControls />
          </div>
        </div>
      }
    />
  );
});
export default TimerMaster;
