// @flow

import jwtDecode from 'jwt-decode';

import sessionStore from 'stores/SessionStore';

import type { WebexApplication } from 'models/WebexSDK';

const initWebexSDK = async () => {
  if (window.Webex) {
    const webexApp: WebexApplication = new window.Webex.Application();

    sessionStore.setWebexApp(webexApp);
    try {
      await sessionStore.webexApp?.onReady();
      // await sessionStore.webexApp?.clearShareUrl();
    } catch (error) {
      throw new Error('Could not initialize Webex SDK');
    }

    if (!sessionStore.webexApp) {
      throw new Error('Webex SDK is not initialized');
    }

    sessionStore.setDisplayContext(webexApp.displayContext);
    sessionStore.setTheme(webexApp.theme);

    const user = await sessionStore.webexApp.context.getUser();
    // $FlowIssue[incompatible-use]
    const meeting = await sessionStore.webexApp.context.getMeeting();

    // use derived conference id for all
    const derivedConferenceId = jwtDecode(user.token)['com.cisco.context.uuid.conferenceid.derived'];
    meeting.conferenceId = derivedConferenceId;

    sessionStore.setUser(user);
    sessionStore.setMeeting(meeting);
  }
};

export default initWebexSDK;
